import React from "react";
import Box from "grommet/components/Box";
import Animate from "grommet/components/Animate";

export default class Index extends React.Component {
  constructor() {
    super();
    this.state = {
      color: undefined
    }
  }
  componentDidMount() {
    let colors = this.props.setColorOfContent();
    this.setState({
      color: colors.home
    });
  }

  render() {
    let style = {
      backgroundColor: this.state.color + "1a"
    }
    return (
      <Animate
        enter={{ animation: "slide-right", duration: 300, delay: 0 }}
        keep={true}
      >
        <Box className="content-box index" style={style}>
          <h1 className="content-title">About</h1>
          <p>After years in video production I made the transition to web development. And although I love to code I still enjoy fiddling with <a href="https://www.adobe.com/products/aftereffects.html" target="_blank">After Effect</a>. Two of my favorite web technologies are <a href="https://reactjs.org/" target="_blank">React</a> and <a href="https://greensock.com/" target="_blank">Gsap</a>, they move the web.</p>
          <p>I also enjoy cycling and comics.</p>
        </Box>
      </Animate>
    );
  }
}



